<script>
import { mapGetters } from "vuex";

import dateFomat from "@/mixin/dateFomat";
import myMixin from "@/mixin/mixin";
import MobileHeader from "../../components/common/MobileHeader.vue";
import MobilePointDash from "../../components/main/MobilePointDash.vue";

// import TermPopup2 from "@/components/common/TermPopup2"
export default {
  name: "MainPage",
  components: { MobileHeader, MobilePointDash },
  mixins: [myMixin, dateFomat],
  layout: "admin",
  props: {},

  data() {
    return {
      //  new code
      period: `${new Date().getFullYear()} ${new Date().getMonth() +
        1} ~ ${new Date().getFullYear()} ${new Date().getMonth() + 1}`,

      accountFlag: false,

      member: {
        bank: null,
        bankNumber: null
      },

      obituary: {
        id: null,
        obituaryName: null,
        obituaryAge: null,
        obituaryAgeYn: null,
        obituarySex: null,
        obituarySexYn: null,
        obituaryMortuary: null,
        obituaryCode: null,
        obituaryDieYn: null,
        obituaryDieDate: null,
        obituaryEnterDate: null,
        obituaryCasketDate: null,
        obituaryCasketYn: null,
        obituaryReligionDate: null,
        obituaryReligionYn: null,
        obituaryEndDate: null,
        obituaryPlace: null,
        obituaryText: null,
        page: {
          page: this.$route.query.page ? this.$route.query.page : 0,
          size: this.$route.query.size ? this.$route.query.size : 8,
          sort: this.$route.query.sort ? this.$route.query.sort : "regDate,desc"
        }
      },

      bank: [
        { name: "", value: "0002" },
        { name: "", value: "0003" },
        { name: "", value: "0004" },
        { name: "", value: "0007" },
        { name: "", value: "0011" },
        { name: "", value: "0020" },
        { name: "SC", value: "0023" },
        { name: "", value: "0027" },
        { name: "", value: "0031" },
        { name: "", value: "0032" },
        { name: "", value: "0034" },
        { name: "", value: "0035" },
        { name: "", value: "0037" },
        { name: "", value: "0039" },
        { name: "", value: "0045" },
        { name: "", value: "0048" },
        { name: "", value: "0050" },
        { name: "", value: "0064" },
        { name: "", value: "0071" },
        { name: "", value: "0081" },
        { name: "", value: "0088" },
        { name: "", value: "0089" },
        { name: "", value: "0090" }
      ],

      // pre code
      secessionPw: "",
      password: "",

      endFlag: "ing",
      searchTxt: "",
      searchType: "name",

      page: 0,
      pages() {
        const list = [];
        for (let i = this.startPage; i <= this.endPage; i += 1) list.push(i);
        return list;
      }
    };
  },
  computed: {
    ...mapGetters([
      "getMainNoticeList",
      "getObituaryList",
      "getObituaryPage",
      "getObituaryPageTotal",
      "getObituaryTotalElements",
      "getObituaryCount",
      "getObituaryReadCount",
      "memberInfo"
    ]),

    startPage() {
      return parseInt((this.getObituaryPage - 1) / 5) * 5 + 1;
    },
    endPage() {
      return (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5 <
        this.getObituaryPageTotal
        ? (parseInt((this.getObituaryPage - 1) / 5) + 1) * 5
        : this.getObituaryPageTotal;
    },

    mobile() {
      return (
        this.$vuetify.breakpoint.name === "xs" ||
        this.$vuetify.breakpoint.name === "sm"
      );
    },
    years: {
      get() {
        return this.$store.getters.getYears;
      }
    },
    months: {
      get() {
        return this.$store.getters.getMonths;
      }
    },
    selectStartYear: {
      get() {
        return `${this.$store.getters.getStartYear}`;
      },
      set(value) {
        return this.$store.commit("setStartYear", value);
      }
    },
    selectStartMonth: {
      get() {
        return String(this.$store.getters.getStartMonth);
      },
      set(value) {
        if (this.years.length > 1 && (value === "11" || value === "12")) {
          this.$store.commit("setStartYear", this.years[0]);
        } else {
          this.$store.commit("setStartYear", this.years[this.years.length - 1]);
        }
        return this.$store.commit("setStartMonth", value);
      }
    },
    selectEndYear: {
      get() {
        return String(this.$store.getters.getEndYear);
      },
      set(value) {
        return this.$store.commit("setEndYear", value);
      }
    },
    selectEndMonth: {
      get() {
        return String(this.$store.getters.getEndMonth);
      },
      set(value) {
        if (this.years.length > 1 && (value === "11" || value === "12")) {
          this.$store.commit("setEndYear", this.years[0]);
        } else {
          this.$store.commit("setEndYear", this.years[this.years.length - 1]);
        }
        return this.$store.commit("setEndtMonth", value);
      }
    }
  },

  created() {
    const nowMonth = new Date().getMonth() + 1;
    switch (nowMonth) {
      case 1:
        this.$store.commit("setYears", [
          `${new Date().getFullYear() - 1}`,
          `${new Date().getFullYear()}`
        ]);
        this.$store.commit("setMonths", [`11`, `12`, `${nowMonth}`]);
        break;

      case 2:
        this.$store.commit("setYears", [
          `${new Date().getFullYear() - 1}`,
          `${new Date().getFullYear()}`
        ]);
        this.$store.commit("setMonths", [
          `12`,
          `${nowMonth - 1}`,
          `${nowMonth}`
        ]);
        break;

      default:
        this.$store.commit("setYears", [`${new Date().getFullYear()}`]);
        this.$store.commit("setMonths", [
          `${nowMonth - 2}`,
          `${nowMonth - 1}`,
          `${nowMonth}`
        ]);
        break;
    }
    this.$store.dispatch("REQUEST_MAIN");

    this.requestGetObituaryList();
  },
  beforeDestroy() {
    this.$store.commit("OBITUARY_INIT");
  },
  methods: {
    backRouting() {
      this.$router.go(-1);
    },

    requestGetObituaryList() {
      const parameter = {
        obituary: this.obituary,
        endFlag: this.endFlag,
        searchTxt: this.searchTxt,
        searchType: this.searchType
      };
      this.$store.dispatch("REQUEST_OBITUARY_LIST", parameter);
    },

    memberLeader(obituary) {
      const leader = obituary.obituaryObituaryMember.filter(
        v => v.obituaryMemberLeader === true
      );

      if (leader?.length) return leader[0];

      return obituary.obituaryObituaryMember[0];
    },
    onPageChange(val) {
      this.page = val - 1;

      this.requestGetObituaryList();
    },
    lookUpChoicePeriod() {
      const startPeriod = this.selectStartYear + this.selectStartMonth;
      const endPeriod = this.selectEndYear + this.selectEndMonth;

      const startDate = new Date(
        `${this.selectStartYear}-${this.selectStartMonth}`
      );
      const endDate = new Date(this.selectEndYear, this.selectEndMonth, 0);

      if (endPeriod < startPeriod) {
        return alert("  ");
      }
    },

    accountCertify(memberInfo) {
      const parameter = {
        accountNumber: this.member.bankNumber,
        bankCode: this.member.bank
      };
      this.$store.dispatch("REQUEST_ACCOUNT_CERTIFY", parameter).then(data => {
        if (data.result) {
          if (memberInfo.memberName.indexOf(data.name) > -1) {
            alert(" .");
          } else {
            alert(" ");
          }
        }
      });
    }
  }
};
</script>

